<template>
  <div class="recommendation">
    <ul>
      <li v-for="(item,index) in bestGoods" class="goodItem" @click="sendParams(index)">
          <goodCard v-bind:good="item"></goodCard>
      </li>
    </ul>
  </div>
</template>

<script>
import goodCard from '../goodCard/goodCard.vue';
export default {
  created(){
     this.getBestGoods();
  },
  data(){
    return {
      bestGoods:[],
      bestGoodsUrl:"/dachuang/recommendation"
    }
  },
  components:{
    "goodCard":goodCard
  },
  methods:{
    sendParams:function(index){
      var id=this.bestGoods[index].id;
      this.$router.push('/sale/good?id='+id);
      this.getBestGoods();
    },
    getBestGoods:function(){
      this.$http.get(this.bestGoodsUrl).then(function(res){
        console.log(res.body);
        this.bestGoods=res.body.data;
      },function(err){
        console.log(err);
      })    
    }


  }
}
</script>

<style scoped>
  ul{
     display: inline-block;
     margin: 20px 0;
     list-style: none;
  }
  li{
    display: inline-block;
    padding: 20px 20px 80px 20px;
  }
</style>


